.section {
  padding-top: clamp(4.063rem, 2.538rem + 6.77vw, 8rem);
}

.section-h1 {
  @extend %second-family-700;
  // font-size: 50px;
  font-size: clamp(1.063rem, 0.264rem + 3.55vw, 3.125rem);
  line-height: 1.2;
  letter-spacing: -0.02em;
  color: var(--white);
}

.section-h2 {
  @extend %second-family-700;
  // font-size: 38px;
  font-size: clamp(1.063rem, 0.554rem + 2.26vw, 2.375rem);
  text-transform: uppercase;
  // color: $white;
}

.section-h3,
.page-title-h2 {
  @extend %second-family-700;
  // font-size: 32px;
  font-size: clamp(1.375rem, 1.133rem + 1.08vw, 2rem);
  text-transform: uppercase;
  color: var(--text);

  &--ttn {
    text-transform: none;
  }
}

.section-h4 {
  @extend %second-family-700;
  font-size: 22px;
  color: var(--text);
}

.section-h5 {
  font-weight: 700;
  // font-size: 26px;
  font-size: clamp(1.125rem, 0.931rem + 0.86vw, 1.625rem);
  line-height: 1.3;
}

.section-h6 {
  font-weight: 600;
  // font-size: 20px;
  font-size: clamp(0.875rem, 0.73rem + 0.65vw, 1.25rem);
  line-height: 1.3;

  &--uppercase {
    text-transform: uppercase;
  }
}

.section-head {
  margin-bottom: 20px;
  display: flex;
  align-items: center;
  // gap: 29px;
  gap: clamp(0.813rem, 0.571rem + 1.08vw, 1.438rem);

  @media screen and (width < 425.99px) {
    justify-content: space-between;
  }
}