.btn {
  padding: 12px;
  @extend %second-family-600;
  // font-size: 18px;
  font-size: clamp(0.938rem, 0.865rem + 0.32vw, 1.125rem);
  text-align: center;
  color: var(--white);
  line-height: normal;

  border-radius: 16px;
  // width: 264px;
  // height: 46px;
  background-color: $red;
}

.btn-main {
  padding: 14px 19px 12px 48px;

  width: 132px;
  height: 48px;
  @extend %second-family-800;
  font-size: 16px;
  color: var(--white);
  background-color: var(--red);
  border-radius: var(--border-radius);

  &__line {
    position: absolute;
    left: 22px;
    width: 18px;
    height: 2px;
    border-radius: var(--border-radius);
    background: var(--white);
    @include transition ($transition-property: transform);

    &:nth-child(1) {
      top: 18px;
    }

    &:nth-child(2) {
      top: 23px;
    }

    &:nth-child(3) {
      top: 23px;
    }

    &:nth-child(4) {
      top: 28px;
    }
  }


  &-open &__line:nth-child(1) {
    transform: scaleX(0);
  }

  &-open &__line:nth-child(2) {
    transform: rotate(45deg);
  }

  &-open &__line:nth-child(3) {
    transform: rotate(-45deg);
  }

  &-open &__line:nth-child(4) {
    transform: scaleX(0);
  }

  // &-open .menu {
  //   transform: translateY(0%);
  // }
}

.btn-cart {
  padding: 8px 24px;
  border: 1px solid var(--red);
  // border-radius: 60px;
  border-radius: clamp(0.563rem, -0.671rem + 5.48vw, 3.75rem);
  // width: 146px;
  // height: 42px;
  @extend %second-family-600;
  color: $red;
  background-color: transparent;
  @include transition ($transition-property: (background-color, color));

  @media (any-hover: hover) {
    &:hover {
      color: var(--white);
      background-color: $red;
    }
  }

  @media screen and (width < 425.99px) {
    // width: 100%;
  }
}

.btn-transparent {
  padding: 14px 28px;
  // display: flex;
  // align-items: center;
  // justify-content: center;
  @extend %second-family-600;
  // font-size: 22px;
  font-size: clamp(0.938rem, 0.768rem + 0.75vw, 1.375rem);
  // border-radius: 31px;
  border-radius: clamp(1.125rem, 0.81rem + 1.4vw, 1.938rem);
  background-color: transparent;

  &--bg-white {
    color: var(--white);
    border: 2px solid var(--white);
  }

  &--bg-red {
    color: var(--red);
    border: 2px solid var(--red);
    @include transition ($transition-property: (color, background-color));

    @media (any-hover: hover) {
      &:hover {
        color: $white;
        background-color: $red;
      }
    }
  }

  &--bg-grey {
    // justify-content: center;
    gap: 7px;
    color: $tundora;
    border: 2px solid $tundora;
    @include transition ($transition-property: (color, background-color));

    @media (any-hover: hover) {
      &:hover {
        color: $white;
        background-color: $tundora;
      }
    }
  }

  &--color-text {
    color: var(--text);
    border: 2px solid var(--text);
  }
}

.see-all {
  width: clamp(9.625rem, 8.27rem + 6.02vw, 13.125rem);
  height: clamp(2.125rem, 1.835rem + 1.29vw, 2.875rem);
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 15px;
  @extend %font3-500;
  // font-size: 22px;
  font-size: clamp(0.938rem, 0.768rem + 0.75vw, 1.375rem);
  color: #da9000;
  // padding: 9px 23px 12px 21px;
  // padding: clamp(0.375rem, 0.302rem + 0.32vw, 0.563rem) clamp(0.813rem, 0.571rem + 1.08vw, 1.438rem) clamp(0.563rem, 0.49rem + 0.32vw, 0.75rem) clamp(0.938rem, 0.792rem + 0.65vw, 1.313rem);


  border: 1px solid $peach;
  // border-radius: 16px;
  border-radius: clamp(0.75rem, 0.653rem + 0.43vw, 1rem);
  background: $serenade;
  @include transition ($transition-property: (color, background-color));

  @media (any-hover: hover) {
    &:hover {
      color: $white;
      background-color: $yellow;
    }
  }

  .catalog-menu__svg {
    display: inline-block;
    width: clamp(0.438rem, 0.317rem + 0.54vw, 0.75rem);
    height: clamp(0.5rem, 0.403rem + 0.43vw, 0.75rem);
    // margin-left: clamp(0.688rem, 0.591rem + 0.43vw, 0.938rem);
    fill: currentColor;
    color: inherit;
    // @include transition ($transition-property: fill);
  }
}

.send-check {
  text-decoration: underline;
}

.btn-flex {
  display: flex;
  align-items: center;
}