  font-size: 14px;
  color: var(--text);
  line-height: 1.5714285714;
  height: 44px;
}
.go-login__item:not(:last-child) {
  margin-bottom: 8px;
}
.go-login__item--sale {
  background: url(../images/icons/go-login-sale.svg) left center/45px 44px no-repeat;
}
.go-login__item--delivery {
  background: url(../images/icons/go-login-delivery.svg) left center/45px 44px no-repeat;
}
.go-login__item--stock {
  background: url(../images/icons/go-login-stock.svg) left center/45px 44px no-repeat;
}
.go-login .modal-overlay {
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.7);
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
  width: 100%;
  opacity: 0;
  visibility: hidden;
  transition: all 0.3s ease-in-out;
}
.go-login .modal-login {
  background-color: #fff;
  width: 300px;
  height: 300px;
  display: flex;
  align-items: center;
  justify-content: center;
  display: none;
  outline: 1px solid red;
}
.go-login .modal-overlay--visible {
  opacity: 1;
  visibility: visible;
  transition: all 0.3s ease-in-out;
}
.go-login .modal--visible {
  display: block;
}

.tooltip {
  position: relative;
}

.tooltiptext {
  position: absolute;
  padding: 4px;
  font-size: 16px;
  color: var(--textlight);
  text-align: start;
  background-color: #f0f0f0;
  border: 1px solid #c4c4c4;
  border-radius: 8px;
  top: calc(100% + 4px);
  left: 50%;
  transform: translate(-50%, 0);
  box-shadow: 0px 4px 20px 0px rgba(0, 0, 0, 0.05);
  pointer-events: none;
  opacity: 0;
  transition: 1s;
}

.tooltip:hover .tooltiptext {
  opacity: 1;
  z-index: 12;
}

.nav__list {
  display: flex;
  align-items: center;
  gap: clamp(0.438rem, 0.268rem + 0.75vw, 0.875rem);
}
.nav__link {
  padding: clamp(0.438rem, 0.268rem + 0.75vw, 0.875rem) clamp(0.375rem, 0.109rem + 1.18vw, 1.063rem) clamp(0.563rem, 0.442rem + 0.54vw, 0.875rem) clamp(1.563rem, 1.054rem + 2.26vw, 2.875rem);
  font-size: clamp(0.688rem, 0.567rem + 0.54vw, 1rem);
  color: var(--cod-gray);
  border: 0.51px solid rgba(255, 169, 0, 0.45);
  border-radius: clamp(0.5rem, 0.306rem + 0.86vw, 1rem);
  background-color: #fff8eb;
  background-position: left clamp(0.375rem, 0.181rem + 0.86vw, 0.875rem) center;
  background-repeat: no-repeat;
  background-size: clamp(1rem, 0.758rem + 1.08vw, 1.625rem) clamp(1rem, 0.758rem + 1.08vw, 1.625rem);
  transition-duration: 350ms;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
}
@media (any-hover: hover) {
  .nav__link:hover {
    border: 1px solid rgba(255, 169, 0, 0.45);
    background-color: #ffe8bc;
  }
}
.nav__link--supermarket {
  background-image: url("../images/icons/icon-link1.png");
}
.nav__link--cooking {
  background-image: url("../images/icons/icon-link2.png");
}
.nav__link--freezing {
  background-image: url("../images/icons/icon-link3.png");
}
.nav__link--other {
  background-image: url("../images/icons/icon-link4.png");
}
.nav__link--stock {
  background-image: url("../images/icons/icon-link5.png");
}
.nav__link--shops {
  background-image: url("../images/icons/icon-link6.png");
}

.page-title:not(:last-child) {
  margin-bottom: clamp(0.813rem, -0.47rem + 5.7vw, 4.125rem);
}

.page-section {
  margin-top: clamp(0.688rem, -0.014rem + 3.12vw, 2.5rem);
}

.section-catalog {
  width: clamp(34.75rem, 16.992rem + 78.92vw, 80.625rem);
  padding-top: 20px;
  display: flex;
  gap: 22px 24px;
}
.section-catalog:last-child {
  margin-bottom: 20px;
}
.section-catalog__link {
  position: relative;
  width: clamp(8.188rem, 4.002rem + 18.6vw, 19rem);
  height: clamp(8.188rem, 4.002rem + 18.6vw, 19rem);
  font-size: clamp(0.813rem, 0.401rem + 1.83vw, 1.875rem);
  line-height: 1.3;
  border-radius: clamp(1.563rem, 0.764rem + 3.55vw, 3.625rem);
  background-position: right bottom;
  background-repeat: no-repeat;
  background-size: auto;
  transition-duration: 350ms;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
}
@media (any-hover: hover) {
  .section-catalog__link:hover {
    transform: translatey(-10px);
    box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.25);
  }
}
.section-catalog__link span {
  position: absolute;
  content: "";
  top: 15.5%;
  left: 13%;
}
@media screen and (width < 1024.99px) {
  .section-catalog__link {
    background-size: contain;
  }
}

.promocode {
  margin-top: clamp(3.688rem, 2.986rem + 3.12vw, 5.5rem);
  color: #ffffff;
  border: 2px solid var(--white);
  border-radius: clamp(1.125rem, 0.496rem + 2.8vw, 2.75rem);
  background: radial-gradient(1404.49% 180.6% at 73.62% -94.51%, rgb(255, 31, 0) 0%, rgb(255, 119, 21) 100%);
  padding-left: clamp(1.313rem, 0.272rem + 4.62vw, 4rem);
  padding-top: clamp(1.688rem, 1.228rem + 2.04vw, 2.875rem);
  padding-right: clamp(0.438rem, -0.724rem + 5.16vw, 3.438rem);
  padding-bottom: clamp(1.438rem, 1.026rem + 1.83vw, 2.5rem);
}
.promocode__box {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  gap: 15px;
}
.promocode__content {
  display: flex;
  justify-content: space-between;
  gap: 15px;
}
.promocode__text {
  margin-top: 7px;
  display: block;
  font-size: clamp(0.813rem, 0.595rem + 0.97vw, 1.375rem);
  text-transform: none;
}
.promocode__head-plus {
  font-weight: 700;
  font-size: clamp(1.5rem, 1.161rem + 1.51vw, 2.375rem);
}
.promocode__btn {
  margin-top: -9px;
  margin-left: auto;
}
@media screen and (width < 1097.99px) {
  .promocode__content {
    width: 100%;
    gap: 10px;
  }
  .promocode__head {
    line-height: 1.5;
  }
  .promocode__text {
    margin-top: 2px;
  }
  .promocode__head-plus {
    align-self: center;
  }
  .promocode__btn {
    margin: 0;
  }
}
@media screen and (width < 504.99px) {
  .promocode__content-left {
    width: 130px;
  }
}

.rating {
  margin: 0;
  padding: 0;
  border: 0;
  display: flex;
  align-items: flex-end;
  justify-content: space-between;
}
.rating__group {
  position: relative;
  display: flex;
  gap: clamp(0.5rem, 0.258rem + 1.08vw, 1.125rem);
  flex-direction: row-reverse;
}
.rating__input {
  appearance: none;
  position: absolute;
  width: 1px;
  height: 1px;
  overflow: hidden;
}
.rating__input:checked + .rating__label::before, .rating__input:checked ~ .rating__label::before {
  content: url("../images/rate-store/star-gold.png");
}
.rating__label {
  display: inline-block;
  position: relative;
  cursor: pointer;
}
.rating__label::before {
  content: url("../images/rate-store/star-grey.png");
}
.rating__label:hover::before, .rating__label:hover ~ .rating__label::before {
  content: url("../images/rate-store/star-gold.png");
}

.rating:hover .rating__input:checked ~ .rating__label::before {
  content: url("../images/rate-store/star-gold.png");
}

.rating .rating__input:checked ~ .rating__label::before {
  content: url("../images/rate-store/star-gold.png");
}